import head from '@/components/admin/AHeader.vue';
import logo from '@/components/Logo.vue';




//
const headerCom = (opt) => {
  const optdefault = { txt: 'error', tag: 'h1', class: 'my-2 text-center' };
  opt = Object.assign({}, optdefault, opt);
  return {
    render: (createElement) => {
      const h = createElement;
      const node = (prop) => {
        return h(
          prop.tag,
          {
            // domProps: {
            //   innerHTML: prop.txt
            // },
            class: prop.class
          },
          [prop.txt],
        );
      };

      return node(opt);
      // return h(
      //   opt.tag,
      //   {
      //     domProps: {
      //       innerHTML: opt.txt
      //     },
      //     class: opt.class
      //   },
      // );
    }
  };
};

 //querygetqueryurl
//paramspostparamsurl

const adminRoute={
  //
  path: '/admin',
  name: "",
  // component: () => import('@/components/admin/Layout.vue'),
  components: {
    header: head,
    body: () => import('@/components/admin/Layout.vue'),
    footer: ''
  },
  //logo
  children: [
    {
      path: '',
      name: 'admin',
      components: {
        title: headerCom({txt:""}),
        container: logo,
      }
    },
    {
      path: 'equipment/info',
      name: 'equipment-info',
      components: {
        // title: headerCom({ txt: '""' }),
        container: () => import('@/components/admin/equipment/Information.vue'),
      }
    },
    {
      path: 'equipment/data',
      name: 'equipment-data',
      components: {
        // title: headerCom({ txt: '""' }),
        container: () => import('@/components/admin/equipment/module/DataTable.vue'),
      }
    },
    {
      path: 'equipment/chart',
      name: 'equipment-chart',
      components: {
        // title: headerCom({ txt: '""' }),
        container: () => import('@/components/admin/equipment/Chart.vue'),
      }
    },
    {
      path: 'test/httpapi',
      name: 'emqx-http-api',
      components: {
        // title: headerCom({ txt: '""' }),
        container: () => import('@/components/admin/test/emqx/TheEmqxHttpApi.vue'),
      }
    },
    {
      path: 'test/mqttclient',
      name: 'emqx-mqtt-client',
      components: {
        // title: headerCom({ txt: '""' }),
        container: () => import('@/components/admin/test/emqx/TheMqttClient.vue'),
      }
    },
    {
      path: 'test/postdata',
      name: 'post-data',
      components: {
        // title: headerCom({ txt: '""' }),
        container: () => import('@/components/admin/test/PostData.vue'),
      }
    },
    {
      path: 'module',
      name: 'module',
      components: {
        // title:headerCom({txt:'""'}),
        container: () => import('@/components/admin/module/TheModule.vue'),
      }
    },
  ],
};

//routenamearr
const getPages=(route,arr)=>{
  const childrenArr=Object.keys(route).includes('children')?route.children:[];

  if(childrenArr.length){
    childrenArr.forEach(el => {
     return getPages(el,arr);
    });
  }

  //
  if(childrenArr.length==0){
    arr.push(route.name);
    // return arr;
  }
  return arr;
};
      
export const pages=getPages(adminRoute,[]);

export default adminRoute;

